* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Poppins", sans-serif;
}
.ap-max {
  // max-width: 1920px;
  max-width: 1440px;
  margin: 0 auto;
}
.ap-header-wrapper {
  box-shadow: 0 3px 5px -1px rgb(0 0 0 / 10%), 0 2px 1px -1px rgb(0 0 0 / 5%);
  position: fixed;
  background-color: $white;
  width: 100%;
  z-index: 2;
}

input,
textarea {
  border-radius: 0;
  border: 1px solid $light-grey;
  box-shadow: none;
  outline: none;
  transition: $transition-all-4s;

  padding-left: 4px;
  padding-right: 4px;
  &:active,
  &:focus {
    border: 1px solid $primary-color;
  }
  // &:hover{}
}
input {
  height: 50px;
}
textarea {
  width: 100%;
}
.ap-danger {
  color: $danger;
}
.ap-top {
  padding-top: 80px;
  min-height: calc(100vh - 60px);
  overflow: hidden;
}

smart-image {
  display: block;
  width: 100%;
  height: 0;
  padding: 0;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;

  &[mode="cover"] {
    height: 100%;
  }
}

.dash-text {
  position: relative;
  margin-bottom: 14px;

  &::after {
    content: "";
    background-color: $primary-color;
    position: absolute;
    bottom: -10px;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100px;
    height: 4px;
  }
  &.dash-left {
    &::after {
      left: 0;
      transform: none;
    }
  }
}

.ap-empty-3 {
  height: 3rem;
}

// breadcrumb
.ap-bread-wrapper {
  display: flex;
  justify-content: space-between;
  padding: 2.5rem 0;
}
.ap-breadcrumb {
  background-color: $primary-color;
  color: $white;

  .bread-page-location {
    display: flex;
    align-items: center;
  }
  .ap-h1 {
    font-weight: 500;
    text-transform: uppercase;
  }

  .bread-list {
    margin: 0;
  }
  .bread-section {
    font-size: 14px;
    list-style: none;
    display: inline-block;
    text-transform: capitalize;

    &:not(:first-child):before {
      content: "/\000a0";
      display: inline-block;
      font-size: 11px;
      margin-left: 6px;
      color: $white;
    }

    a {
      color: $white;
      transition: $transition-all-4s;

      &:hover {
        color: $dark-white;
        text-decoration: none;
      }
    }
  }
}
.ap-head {
  margin-bottom: 1.5rem;
}

// base ul
.ap-list {
  list-style: none;
  .ap-list-item {
    &::before {
      content: "";
      height: 7px;
      width: 7px;
      display: inline-block;
      background-color: $primary-color;
      margin-right: 6px;
      margin-bottom: 2px;
    }
  }
}

.ap-box {
  box-shadow: 0 0 20px 2px #e9eaeb;
  @include transition(all 0.3s ease);

  &:hover {
    box-shadow: 0 0 20px 2px #dbdbdb;
  }
}
.ap-overlay {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  background-color: transparent;
  transition: $transition-all-4s;
  &:hover {
    background-color: rgba($black, 0.1);
  }
}
.ap-col-group {
  position: relative;
}

// message
.ap-about-message {
  background-color: $primary-color;
  color: $white;
  .msg-wrapper {
    background-color: $white;
    color: $primary-color;
    padding: 4rem;
    position: relative;
    margin-top: 40px;
    &::before {
      left: 20%;
    }
    &::before,
    &::after {
      content: "";
      height: 40px;
      width: 4px;
      background-color: $white;
      position: absolute;
      top: -40px;
      border-radius: 20px 20px 0 0;
    }
    &::after {
      right: 20%;
    }
  }
  .ap-span {
    text-align: center;
    font-weight: 300;
    display: block;
    font-size: 13px;
    font-style: italic;
  }
  .ap-p {
    color: $secondary-dark;
  }
}

// footer style
.ap-footer {
  height: 60px;
  background-color: $primary-color;
  font-size: 14px;

  .footer-wrapper {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: $white;
    text-align: center;
    p {
      margin: 0;
    }

    padding: 0 14px;
  }
}
